'use strict';
const spawn = require('child_process').spawn;
import Logger from './logger.js';
import config from './sources/config.json';
const logger = new Logger();
const inquirer = require('inquirer');
const logUpdate = require('log-update');


export default class {

  spawn(command, args) {
    return spawn(command, args);
  }

  cp(path, destination) {
    const cp = spawn('cp', [path, destination]);

    cp.stderr.on('data', data => {
      if (config.debug) {
        logger.warn(data.toString());
      }
    });
  }

  backup(paths) {
    for (let path of paths) {
      this.logUpdate('backing up');
      this.cp(path, `${path}.backup`);
    }
  }

  prompt(questions) {
    return new Promise((resolve, reject) => {
      inquirer.prompt(questions).then(answers => {
        resolve(answers)
      }).catch(error => {
        reject(error);
      });
    });
  }

  logUpdate(message) {
    logUpdate(logger._chalk(message, 'cyan'));
  }

  online() {
    return new Promise((resolve, reject) => {
      const ping = spawn('ping', ['google.com']);

      ping.stdout.on('data', data => {
        ping.kill()
      });

      ping.stderr.on('data', data => {
        ping.kill()
      });

      ping.on('close', (code) => {
        if (code === 0 || code === null) {
          resolve(true);
        } else {
          resolve(false);
        }
        if (global.debug) {
          logger.warn(`child process exited with code ${code}`);
        }
      });
    });
  }
}
